<script lang="ts">import { msg } from "@/stores";
import { onMount } from "svelte";
let face;
function speak(text) {
    const mouth = this.querySelector(".mouth");
    const utterance = new SpeechSynthesisUtterance(text);
    let mouthAnimation;
    utterance.onstart = () => {
        mouthAnimation = mouth.animate([
            { transform: "scale(1)" },
            { transform: "scale(1.2)" },
            { transform: "scale(1)" },
        ], {
            duration: 100,
            iterations: Infinity,
        });
    };
    speechSynthesis.speak(utterance);
    return new Promise((res) => {
        utterance.onend = () => {
            mouthAnimation.cancel();
            res(utterance);
        };
    });
}
onMount(() => {
    face.speak = speak;
    face.addEventListener("click", () => {
        msg.update((msg) => msg === "Hello World!" ? "Goodbye Moon!" : "Hello World!");
        face.speak(`Hello! What is your name?`).then(() => {
            const speechRecognition = new webkitSpeechRecognition();
            speechRecognition.onresult = ({ results }) => {
                const transcript = results[0][0].transcript;
                const userName = transcript.replace(/hello|hi|my|name|is/g, "");
                face.speak(`I love you, ${userName}`);
            };
            speechRecognition.start();
        });
    });
});
</script>

